@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,400,700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

.header {
  background-color: #ffffff;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
  box-shadow: rgb(238, 238, 238) 0px 1px 0px 0px;
}

.header__wrapper {
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0px auto;

  display: flex;
  align-items: center;
  width: 1200px;
  height: 5rem;
  color: #2c2c2c;
}

.header .header__wrapper i {
  visibility: hidden;
  padding: 0 20px;
}

.header .header__wrapper .desktop__header {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1024px) {
  .header__wrapper {
    top: auto;
    top: initial;
    bottom: 0px;
    width: 100%;
    height: 80px;
  }

  .header .header__wrapper i {
    visibility: visible;
  }
}

.header__wrapper a {
  display: flex;
  align-items: center;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: inherit;
  padding: 0 20px;

  font-size: 1rem;
  height: 50px;
}

.header__wrapper a:hover {
  color: #93acff;
}

.header__wrapper .mobile__header {
  background-color: #ffffff;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 20;
  width: 50%;
  height: 100%;
  box-shadow: rgb(238, 238, 238) 0px 0px 1px 1px;
}

.header__wrapper .mobile__header i {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 50px;
}

